/* Showing animation: width first, then opacity */
@keyframes show {
    0% {
      width: 10px;
      opacity: 0;
    }
    80% {
      width: 220px;
      opacity: 0;
    }
    100% {
      width: 220px;
      opacity: 1;
    }
  }
  
  /* Hiding animation: opacity first, then width */
  @keyframes hide {
    0% {
      width: 220px;
      opacity: 1;
    }
    20% {
      width: 220px;
      opacity: 0;
    }
    100% {
      width: 10px;
      opacity: 0;
    }
  }
