.rsvp-button-container {
    display: flex;
    gap: 4px;
    margin-top: 8px;
    
    .rsvp-btn {
        flex: 1;
        padding: 6px 8px;
        border: 1px solid #ddd;
        border-radius: 6px;
        background: white;
        color: #666;
        font-size: 11px;
        font-weight: 500;
        cursor: pointer;
        transition: all 0.2s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 4px;
        min-height: 28px;
        
        &:hover:not(:disabled) {
            background: #f8f9fa;
            border-color: #007bff;
            color: #007bff;
        }
        
        &:disabled {
            opacity: 0.5;
            cursor: not-allowed;
        }
        
        .button-text {
            flex: 1;
            text-align: center;
            b{
                border-radius: 100px;
                padding: 2px 6px;
                background-color: var(--blue);
                color: white;
                margin-right:3px;
            }
        }
        &.active {
            border-color: #4CAF50;
            background: rgba(76, 175, 80, 0.1);
            color: #4CAF50;
            
            &.going {
                border-color: #4CAF50;
                background: rgba(76, 175, 80, 0.1);
                color: #4CAF50;
                .button-text b{
                    background-color: var(--green-main);
                }
            }
            
            &.maybe {
                border-color: #FF9800;
                background: rgba(255, 152, 0, 0.1);
                color: #FF9800;
                .button-text b{
                    background-color: #FF9800;
                }
            }
            
            &.not-going {
                border-color: #F44336;
                background: rgba(244, 67, 54, 0.1);
                color: #F44336;
                .button-text b{
                    background-color: #FF9800;
                }
            }
        }
        
        svg {
            width: 12px;
            height: 12px;
        }
        

    }
}

.rsvp-button {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 6px 8px;
    border-radius: 6px;
    font-size: 11px;
    font-weight: 500;
    margin-top: 8px;
    
    &.deadline-passed {
        background: rgba(255, 152, 0, 0.1);
        color: #FF9800;
        border: 1px solid rgba(255, 152, 0, 0.3);
    }
    
    &.capacity-reached {
        background: rgba(244, 67, 54, 0.1);
        color: #F44336;
        border: 1px solid rgba(244, 67, 54, 0.3);
    }
    
    svg {
        width: 12px;
        height: 12px;
    }
}

.rsvp-registration-form-modal {
    .rsvp-registration-form-modal-inner {
        max-height: 90vh;
        display: flex;
        flex-direction: column;
        background: var(--bg, #fff);
        border-radius: 12px;
        overflow: hidden;
    }

    .rsvp-registration-form-modal-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 1rem 1.25rem;
        border-bottom: 1px solid var(--border, #e5e7eb);

        h3 {
            margin: 0;
            font-size: 1.125rem;
        }
    }

    .rsvp-registration-form-modal-close {
        padding: 0.25rem;
        border: none;
        background: none;
        cursor: pointer;
        color: var(--text-secondary, #6b7280);

        &:hover {
            color: var(--text-primary, #111);
        }
    }

    .rsvp-registration-form-modal-body {
        overflow-y: auto;

        .form-viewer .submit-button {
            margin-top: 1rem;
        }
    }
}
